











































	import uniIcon from "@/components/uni-icon/uni-icon.vue"
	export default {
		components: {uniIcon},
		data() {
			return {
				inputName:'', //用户名
				inputPwd: '', //密码
				showPassword: true,
			}
		},
		methods:{
			changePassword: function() {
				this.showPassword = !this.showPassword;
			},
			loginName : function(event){
				this.inputName = event.target.value;
			},
			loginPwd: function function_name(event){
				this.inputPwd = event.target.value;
			},
			login(e){
				console.log(125745);
				if(this.inputName ==""&& this.inputPwd ==""){
					uni.showToast({
						title:"用户名或密码不能为空",
						icon: "none"
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/index/dologin', //接口地址
						method: 'GET', //请求方式
						//传递的参数
						data:{
							name:this.inputName,
							passwords:this.inputPwd
						},
						//请求成功后的回调函数
						success:(res) => {
							//判断返回的状态  200--成功
							if(res.data.status == 200){
								var loginData = new Object();  //声明一个数组
								loginData.csid = res.data.csid; //csid:
								loginData.dbname = res.data.dbname; //数据库名字
								loginData.dis = res.data.dis; //区分主账户还是子账户
								loginData.org = res.data.org; //请求的域名地址
								loginData.shop = res.data.shop; //数据库的数量
								loginData.softdog = res.data.softdog; //加密狗
								loginData.Ident_Signboard = res.data.tokenInfo.Ident_Signboard; //令牌
								loginData.Ident_Signguid = res.data.tokenInfo.Ident_Signguid; //Guid：识别用户身份
								
								var json = JSON.stringify(loginData); //转换JSON格式
								uni.setStorageSync('jsonList',json) //存储进去
								/* var wulala=uni.getStorageSync("jsonList");
								var data = eval( '(' + wulala + ')' ); //JSON字符串转对象
								console.log(data.csid); //取单个数据
								console.log(typeof(wulala)); 
								console.log(wulala); */
								uni.switchTab({
									url:'index'
								});
							}else{
								uni.showToast({
									title:'登录失败，账号或密码错误',
									icon:'none',
								})
							}
						}
					})
				}


			},
			
// 			ceshi(e){
// 				const value = uni.getStorageSync('jsonList');
// 				console.log(value);
// 				const org = uni.getStorage({key:'org'});
// 				console.log(org)
// 			}
		},
		
		
	}
